<template lang="pug">
    ul
        li.subject__item-title Преподаватели:
        li.subject__item-container(v-for="item,idx in subjectTeachers") {{idx+1}}) {{item.lastName}} {{item.firstName}}
    
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
    data() {
        return {
        }
    },
    created() {
        this.fetchSubjectTeachers(this.subject.id)
    },
    computed: {
        ...mapState("subjectTeachers",{
            subjectTeachers: state => state.subjectTeachers
        })
    },
    props: {
        subject: Object
    },
    methods: {
        ...mapActions('subjectTeachers', ['fetchSubjectTeachers'])
    }
}
</script>
